<template>
  <div class="darkbox-container">

    <div class="loader-container-thumbs" ref="lt">
      <img class="loader-thumbs"  :src="'/static/loader/loader.svg'" alt="prodentica loader">
    </div>

    <div class="darkbox-box" ref="db">
      <i class="fa fa-times-circle close" aria-hidden="true" @click="closeDarkbox"></i>
      <div class="loader-container" v-bind:style="styleLoader">
        <img class="loader" :src="'/static/loader/loader.svg'" alt="prodentica loader">
      </div>
      <div class="img-container" ref="ic" @click="closeDarkbox">
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      styleLoader: {
        display: 'block'
      }
    }
  },
  methods: {

    //open image container
    openDarkbox() {
      this.$refs.db.style.display = 'block';
    },

    //close image container
    closeDarkbox() {
      this.$refs.ic.firstChild.remove();
      this.$refs.db.style.display = 'none';
    },

    //load thumbs with preloader
    getThumbs() {
      let darkbox = document.querySelectorAll('.darkbox');
      let _imgs = [];
      let remaining = darkbox.length;


      for (let a = 1; a < remaining; a++) {
        _imgs[a] = new Image();
        _imgs[a].onload = () => {
               --remaining;
               if (remaining <= 1) {
                 this.$refs.lt.style.display = 'none';
               }
           };
        _imgs[a].src = './static/images/thumbs/galeria0' + a + '.jpg';
      }

    },
    getImages() {
      let imgs = document.querySelectorAll('.darkbox');

      imgs.forEach((el) => {
        el.addEventListener('click', (e) => {

        //preloading add loader
        this.styleLoader.display = 'block';
        this.$refs.ic.style.display = "none";

        //open darbox
        this.openDarkbox();
        let sourceImage = document.createElement('img');
        let filename = e.target.src.replace(/^.*[\\\/]/, '');
        sourceImage.src = '/static/images/' + filename;

        //preloading
        let img = new Image();
        img.onload = () => {

          //preloading remove loader
          this.styleLoader.display = 'none';
          let imgContainer = document.querySelector('.img-container');
          imgContainer.classList.add('fade-in');
          this.$refs.ic.style.display = "block";
        };
        img.src = sourceImage.src;

        //append big image
        this.$refs.ic.appendChild(sourceImage);
        });
      });
    }
  },
  mounted() {
    this.getImages();
    this.getThumbs();
  }
}
</script>

<style>

.darkbox {
  cursor: pointer;
}

.loader-container, .loader-container-thumbs {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader, .loader-thumbs {
  width: 200px !important;
}

.img-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  text-align: center;
}

.darkbox-container img {
  width: 33%;
}

.img-container img {
  border: 15px solid black;
  cursor: pointer;
  width: 100%;
  max-height:100%;
}

.darkbox-box {
  top: 0;
  left: 0;
  position: fixed;
  display: none;
  background-color: rgba(255, 255, 255, 0.7);
  width: 100vw;
  height: 100vh;
  z-index: 100;
}

.darkbox-box i {
  font-size: 2em;
  cursor: pointer;
}

.fade-in {
	-webkit-animation: fade-in 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: fade-in 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

/* ----------------------------------------------
 * Generated by Animista on 2018-3-19 9:1:59
 * w: http://animista.net, t: @cssanimista
 * ---------------------------------------------- */

/**
 * ----------------------------------------
 * animation fade-in
 * ----------------------------------------
 */
@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 599px) {
  .darkbox-container img {
    width: 100%;
  }
  .img-container, .img-container img {
    width: 90%;
  }

}

@media screen and (min-width: 600px) and (max-width: 999px) {

  .darkbox-container img {
    width: 49.5%;
  }

  .img-container, .img-container img {
    width: 90%;
  }

}


</style>



